// ** React Imports
import { useState, useEffect } from 'react'
import { Controller, useForm } from "react-hook-form"
import classnames from 'classnames'
import Select from 'react-select'
import Flatpickr from 'react-flatpickr'
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Componentss
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { Card, Label, Button, Input, FormFeedback, Modal, ModalHeader, ModalBody, ModalFooter, Form, Row, Col, Table, Badge } from 'reactstrap'
// ** Store & Actions
import { leaveList, applyLeave, UpdateLeave, deleteLeave } from './store/index'
import { useDispatch, useSelector } from 'react-redux'
// ** Styles
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import { activeOrganizationid, activeOrganization, orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'
import { useParams } from 'react-router-dom'
import { Edit, Trash2 } from 'react-feather'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import ReactPaginate from 'react-paginate'
const MySwal = withReactContent(Swal)

const activeOrgId = activeOrganizationid()
const userId = orgUserId()
const activeOrg = activeOrganization()

const Leave = (data) => {
  // ** Store Vars
  const designationId = data.designationid || activeOrg.designationid
  //const [leaveDuration, setLeaveDuration] = useState(1)
  const [leaveTypeOptions, setLeaveTypeOptions] = useState([])
  const dispatch = useDispatch()
  const store = useSelector(state => state.profile)
  // ** States
  const [leaveDuration, setLeaveDuration] = useState(1)
  const [value] = useState('')
  const { id } = useParams()
  const [rowsPerPage] = useState(6)
  const [currentPage, setCurrentPage] = useState(1)
  const schema = yup.object().shape({
    organizationId: yup.string().default(activeOrgId),
    createdBy: yup.string().default(userId),
    updatedBy: yup.string().default(userId),
    leaveTypeId: yup.string().required("Please Select LeaveType"),
    fromDate: yup.string().required('Please Enter From Date'),
    duration: yup.string().default(1),
    daysCount: yup.string().default(1),
    toDate: yup.string().when("duration", { is: (duration) => duration === '2', then: yup.string().required("Please Enter To Date.") }),
    userId: yup.string().default(userId),
    reason: yup.string().default(''),
    status: yup.string().default(1)
  })


  const deletefn = (lid) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteLeave({ id:lid, userId: id ?? userId }))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Leave Application has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const getLeaveType = () => {
    axios.post('/leavetypes/list', { designationId }).then(response => {
      const arr = response.data
      setLeaveTypeOptions(arr.leavetypes)
    })
  }

  const { handleSubmit, control, reset, setValue, formState: { errors } } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })
  const [formModal, setFormModal] = useState(false)
  const closeModal = () => {
    setFormModal(false)
  }

  const statuArr = ["", "Applied", "Approved", "Rejected"]
  const statuColorArr = ["", "light-primary", "light-success", "light-warning"]
  const dateFormat = (value) => {

    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }

  }

  const onSubmit = async (leave) => {

    if (parseInt(leave.duration) === 2) {
      const start = moment.unix(leave.fromDate).valueOf()
      const end = moment.unix(leave.toDate).valueOf()
      const duration = moment(end, 'x').diff(moment(start, 'x'), 'days')
      leave.daysCount = String(duration)
    }
    if (leave.id !== undefined && leave.id !== '') {
      await dispatch(UpdateLeave(leave))
    } else {
      await dispatch(applyLeave(leave))
    }
    setFormModal(false)
    reset({ name: '', id: '', organizationId: activeOrgId, fromDate: '', toDate: "", userId, reason: '', status: 1, leaveTypeId: '', daysCount: '1' })
  }

  const editLeave = (val) => {
    reset({ name: val.name, id: val.id, updatedBy: userId, organizationId: activeOrgId, fromDate: String(val.fromdate / 1000), toDate: String(val.todate / 1000), userId: val.userid, reason: val.reason, status: 1, leaveTypeId: (val.leavetypeid), duration: val.duration, daysCount: val.dayscount })
    setFormModal(true)
    setLeaveDuration(parseInt(val.duration))
  }

  const handlePagination = async (page) => {

    setCurrentPage(page.selected + 1)

    await dispatch(
      leaveList({
        q: value,
        page: page.selected + 1,
        perPage: rowsPerPage,
        userId: id || userId,
        status: 'All'
      })
    )
  }

  useEffect(() => {

    if (data.tabId === 'leaves') {
      dispatch(
        leaveList({
          q: value,
          page: currentPage,
          perPage: rowsPerPage,
          userId: id || userId,
          status: 'All'
        })
      )
      getLeaveType()
    }
  }, [data.tabId])

  const dataToRender = () => {
    const filters = {
      userId: id || userId,
      q: value
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.employeeLeaves.length > 0) {
      return store.employeeLeaves
    } else if (store.employeeLeaves.length === 0 && isFiltered) {
      return []
    }
  }
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }
  return (

    <div className='invoice-list-wrapper'>
      <Card>
        <div className='invoice-list-dataTable react-dataTable'>
          <Col className='d-flex justify-content-between align-items-center'>
            <Col className='px-2'>
              <h5>Leave List</h5>
            </Col>
            <Button color='primary' className='m-1' onClick={() => setFormModal(true)}>Apply for Leave</Button>
          </Col>
          <Col className='px-2'>
            <Table>
              <thead>
                <tr>
                  <th>Leave Type</th>
                  <th>Date</th>
                  <th>No of Days</th>
                  <th>Status</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {dataToRender().map((leave, k) => {
                  return (<tr key={k}>
                    <td>{leave.leavetypename}</td>
                    <td>{dateFormat(leave.fromdate)} {leave.duration === "2" ? ` - ${dateFormat(leave.todate)}` : ''} </td>
                    <td>{leave.dayscount}</td>
                    <td>
                      <Badge color={statuColorArr[leave.status]}>{statuArr[leave.status]}
                      </Badge>
                    </td>
                    <td>
                      <div className='column-action d-flex align-items-center'>
                        {leave.status === 1 && (
                          <div>
                            <Edit className='text-body cursor-pointer' size={17} id={`send-tooltip-${leave.id}`} onClick={() => { editLeave(leave) }} />
                            <Trash2 className='text-body cursor-pointer ms-1' size={17} id={`send-tooltip-${leave.id}`} onClick={() => { deletefn(leave.id) }} />
                          </div>
                        )
                        }
                      </div>
                    </td>
                  </tr>)
                })
                }
              </tbody>
            </Table>
            <CustomPagination />
          </Col>
        </div>
        <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
          <ModalHeader toggle={() => closeModal()}>Apply Leave</ModalHeader>
          {

            Object.keys(errors).map((key) => {

              return (<FormFeedback key={key}>${errors[key]?.message}</FormFeedback>)

            })

          }
          <Form onSubmit={handleSubmit(onSubmit)}>
            <ModalBody>
              <div className='mb-0'>
                <Label sm='3' size='lg' className='form-label required' for='leaveTypeId'>
                  Leave Type
                </Label>
                { }
                <Controller
                  control={control}
                  name="leaveTypeId"
                  id="leaveTypeId"
                  render={({ field, ref }) => (
                    <Select
                      {...field}
                      inputRef={ref}
                      className={classnames('react-select', { 'is-invalid': errors.leaveTypeId })}
                      {...field}
                      classNamePrefix='select'
                      options={leaveTypeOptions}
                      value={leaveTypeOptions.find(c => { return c.id === field.value })}
                      onChange={val => { field.onChange(val.id) }}
                      getOptionLabel={(option) => option.name}
                      getOptionValue={(option) => option.id}
                    />
                  )}

                />
                {errors.leaveTypeId && <FormFeedback className='text-danger'>{errors.leaveTypeId?.message}</FormFeedback>}
              </div>
              <div className='mb-0'>
                <Label sm='3' size='lg' className='form-label required' for='name'>
                  Duration
                </Label>
                <Row>
                  <Controller
                    name='duration'
                    id='duration_1'
                    control={control}
                    render={({ field }) => (<Col><Input name='duration' id='duration_1' type='radio' defaultChecked={leaveDuration === 1}
                      {...field} onInput={val => { setLeaveDuration(val.target.value); setValue('duration', val.target.value); return val.target.value }} onChange={val => { field.onChange(val) }} value={1} />
                      <Label for="duration_1" className='ms-1'>Single</Label></Col>)}
                  />

                  <Controller
                    name='duration'
                    id='duration_2'
                    control={control}
                    render={({ field }) => (<Col><Input name='duration' id='duration_2' type='radio' defaultChecked={leaveDuration === 2}
                      {...field} onInput={val => { setLeaveDuration(val.target.value); setValue('duration', val.target.value); return val.target.value }} onChange={val => { field.onChange(val) }} value={2} />
                      <Label for="duration_2" className='ms-1'>Multiple</Label></Col>)}
                  />

                  <Controller
                    name='duration'
                    id='duration_3'
                    control={control}
                    render={({ field }) => (<Col><Input name='duration' id='duration_3' type='radio' defaultChecked={leaveDuration === 3}
                      {...field} onInput={val => { setLeaveDuration(val.target.value); setValue('duration', val.target.value); return val.target.value }} value={3} />
                      <Label for="duration_3" className='ms-1'>Half Day</Label></Col>)}
                  />
                </Row>
              </div>
              <div className='mb-0'>
                <Label sm='3' size='lg' className='form-label required' for='fromDate'>
                  From
                </Label>

                <Controller
                  control={control}
                  id='fromDate'
                  name={`fromDate`}
                  render={({ field }) => (
                    <Flatpickr
                      value={field.value}
                      onChange={(date, dateStr) => { field.onChange(dateStr) }}
                      options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                      className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.toDate })}
                    />
                  )}
                />
                {errors.fromDate && <FormFeedback>{errors.fromDate.message}</FormFeedback>}
              </div>
              {parseInt(leaveDuration) === 2 &&
                <div className='mb-0'>
                  <Label sm='3' size='lg' className='form-label required' for='toDate'>
                    To
                  </Label>
                  <Controller
                    control={control}
                    id='toDate'
                    name={`toDate`}
                    render={({ field }) => (
                      <Flatpickr
                        value={field.value}
                        onChange={(date, dateStr) => { field.onChange(dateStr) }}
                        options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                        className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.toDate })}
                      />
                    )}
                  />
                  {errors.toDate && <FormFeedback>{errors.toDate.message}</FormFeedback>}
                </div>
              }
              <div className='mb-0'>
                <Label sm='3' size='lg' className='form-label' for='reason'>
                  Reason
                </Label>

                <Controller
                  id='reason'
                  name='reason'
                  control={control}
                  render={({ field }) => <Input type="textarea" invalid={errors.reason && true} {...field} />}
                />
                {errors.reason && <FormFeedback>{errors.reason.message}</FormFeedback>}
              </div>


            </ModalBody>
            <ModalFooter>
              <Button type="submit" color='warning' onClick={() => closeModal()}>
                Cancel
              </Button>
              <Button color='primary' type='submit' >
                Save
              </Button>
            </ModalFooter>
          </Form>
        </Modal >

      </Card>
    </div>
  )
}

export default Leave
