// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { useParams, Link, useLocation } from 'react-router-dom'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Store & Actions
import { getUser } from './store'
import { useSelector, useDispatch } from 'react-redux'

// ** Reactstrap Imports
import { Row, Col, Alert } from 'reactstrap'

// ** User View Components
import UserTabs from './Tabs'
import UserInfoCard from './UserInfoCard'

// ** Styles
import '@styles/react/apps/app-users.scss'

const TeamView = () => {
  // ** Store Vars
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const search = useLocation().search
  const searchParams = new URLSearchParams(search)
  const store = useSelector(state => state.profile)
  const dispatch = useDispatch()
  const [uploadCallBack, setUploadCallBack] = useState(false)
  const { id } = useParams()
  useEffect(async () => {
    setBlock(true)
    await setUploadCallBack(uploadCallBack)
    await dispatch(getUser(id))
    setBlock(false)
  }, [dispatch, uploadCallBack])

  const [active, setActive] = useState('jobinfo')

  const toggleTab = tab => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  useEffect(() => {
    const tabId = searchParams.get('tabId')
    if (tabId) {
      setActive(tabId)
    }
  }, [searchParams])

  return store.selectedUser !== null && store.selectedUser !== undefined ? (
    <UILoader blocking={block} loader={<Loader />}>
      <div className='app-user-view'>
        <Row>
          <Col xl='3' lg='3' xs={{ order: 1 }} md={{ order: 0, size: 3 }}>
            <UserInfoCard selectedUser={store.selectedUser} />
          </Col>
          <Col xl='9' lg='9' xs={{ order: 0 }} md={{ order: 1, size: 9 }}>
            <UserTabs active={active} toggleTab={toggleTab} selectedUser={store.selectedUser} uploadCallBack={uploadCallBack} />
          </Col>
        </Row>
      </div>
    </UILoader>
  ) : (
    <UILoader blocking={block} loader={<Loader />}>
      <Alert color='danger'>
        <h4 className='alert-heading'>User not found</h4>
        <div className='alert-body'>
          User with id: {id} doesn't exist. Check list of all Users: <Link to='/apps/user/list'>Users List</Link>
        </div>
      </Alert>
    </UILoader>
  )
}
export default TeamView
