// ** React Imports
import { Fragment, useEffect, useState } from 'react'
// ** Reactstrap Imports
import {
  Card,
  CardBody,
  Table
} from 'reactstrap'
import '@styles/react/libs/flatpickr/flatpickr.scss'

import { getData } from '@src/views/pages/account-settings/Attendance/store/holidays.js'

import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment'
import ReactPaginate from 'react-paginate'


const Holidays = ({tabId}) => {

  const [data, setData] = useState([])
  const rowsPerPage = 10
  const [currentPage, setCurrentPage] = useState(1)
  const sortColumn = 'date'
  const store = useSelector(state => state.holidays)
  const dispatch = useDispatch()

  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }
  }

  const handlePagination = async page => {
    await dispatch(getData({
      sort: sortColumn,
      page: page.selected + 1
    }))

    setCurrentPage(page.selected + 1)

  }

  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }


  useEffect(async () => {
    if (tabId === 'profileholidays') {
      await dispatch(getData({
        sort: sortColumn,
        page: currentPage
      }))
    }

  }, [tabId, currentPage])

  useEffect(() => {
    setData(store.data)
  }, [store.data])

  return (
    <Fragment>
      <Card>

        <CardBody className='py-25'>
          <Table>
            <thead>
              <tr>
                <th>Name</th>
                <th>Date</th>
                
              </tr>
            </thead>
            <tbody>
              {data.map((card, index) => {
                return (
                  <tr key={index}>
                    <td>
                      {card.name}
                    </td>
                    <td>
                      {dateFormat(card.date)}
                    </td>                    
                  </tr>
                )
              })}
            </tbody>
          </Table>
          <CustomPagination />

        </CardBody>
      </Card>
    
    </Fragment>
  )
}

export default Holidays
