// ** React Imports
import { useState, useEffect, Fragment } from 'react'

// ** Table Columns
import { columns } from './attendance_columns'
// ** Third Party Components
import DataTable from 'react-data-table-component'
import { ChevronDown, ChevronLeft, ChevronRight, Download, DownloadCloud, MoreVertical } from 'react-feather'
import { Button, Card, CardBody, CardHeader, DropdownItem, DropdownMenu, DropdownToggle, UncontrolledButtonDropdown } from 'reactstrap'
import { attendaceList } from './store/index'
import { useDispatch, useSelector } from 'react-redux'

// ** Styles
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const Attendance = ({ tabId, selectedUserName, userId }) => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.profile)
  const { id } = useParams()
  // ** States
  const [value] = useState('')
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const rowsPerPage = 0
  const currentPage = 1
  const [statusValue] = useState('')
  const [sort] = useState('desc')
  const [sortColumn] = useState('punchin')
  const [startDate, setStartDate] = useState(moment().startOf('month').startOf('day').valueOf())
  const [endDate, setEndDate] = useState(moment().endOf('month').startOf('day').valueOf())

  useEffect(async () => {
    if (tabId === 'profileattendance') {
      setBlock(true)
      await dispatch(
        attendaceList({
          sort,
          q: value,
          sortColumn,
          page: currentPage,
          perPage: rowsPerPage,
          status: statusValue,
          startDate,
          endDate,
          userId: id || userId
        })
      )
      setBlock(false)
    }
  }, [tabId, userId])

  const dataToRender = () => {
    const filters = {
      status: statusValue,
      userId: id || userId,
      q: value
    }
    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (store.employeeAttendances.length > 0) {
      return store.employeeAttendances
    } else if (store.employeeAttendances.length === 0 && isFiltered) {
      return []
    }
  }

  const changeDate = async (val) => {
    let start_date, end_date
    if (val === 1) {
      start_date = moment(startDate, 'x').add(1, 'M').startOf('day').valueOf()
      end_date = moment(endDate, 'x').add(1, 'M').endOf('day').valueOf()
    } else {
      start_date = moment(startDate, 'x').subtract(1, 'M').startOf('day').valueOf()
      end_date = moment(endDate, 'x').subtract(1, 'M').endOf('day').valueOf()
    }
    setStartDate(start_date)
    setEndDate(end_date)
    setBlock(true)
    await dispatch(
      attendaceList({
        sort,
        q: value,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: statusValue,
        startDate: start_date,
        endDate: end_date,
        userId: id || userId
      })
    )
    setBlock(false)
  }

  const exportData = async () => {
    const params = {
      q: value,
      page: currentPage,
      status: statusValue,
      userId: id || userId,
      perPage: rowsPerPage,
      startDate,
      endDate,
      export: true
    }
    setBlock(true)
    const response = await axios.post(`/employeeattendances/list`, params)
    if (response.data.employeeattendances.employeeattendances.length === 0) {
      toast.error('No Record to export Data', { position: 'bottom-right' })
      return false
    }
    const result = await response.data.employeeattendances.employeeattendances.map((obj) => {

      const data = {}
      data['date'] = dateFormat(obj.actualdate)
      data['punchin'] = timeFormat(obj.punchin)
      data['punchout'] = timeFormat(obj.punchout)
      data['totalhours'] = msToTime(obj.totalhours)

      return data
    })

    const ws = await XLSX.utils.json_to_sheet(result, { origin: 'A2', skipHeader: true })
    const Heading = [['Date', 'Punch In', 'Punch Out', 'Total Hours']]
    XLSX.utils.sheet_add_aoa(ws, Heading)
    const wb = { Sheets: { data: ws }, SheetNames: ["data"] }
    const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" })
    const data = new Blob([excelBuffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8" })
    const month = moment(startDate, 'x').format('MMM')
    FileSaver.saveAs(data, `${selectedUserName}-${month}-Attendance.xlsx`)
    setBlock(false)
  }

  return (
    <div className='invoice-list-wrapper'>
      <Card>
        <CardHeader className='py-1'>
          <UncontrolledButtonDropdown>
            <DropdownToggle color='secondary' outline caret>
              <MoreVertical className='font-small-4 me-50' />
            </DropdownToggle>
            <DropdownMenu end>
              <DropdownItem className='w-100' onClick={() => exportData()}>
                <Download className='font-small-4 me-50' />
                <span>Export</span>
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledButtonDropdown>
          <div className='d-flex'>            
            <div className='border border-1 p-md-50'>
              <ChevronLeft className='cursor-pointer' onClick={() => { changeDate(2) }} />
              {moment(startDate, 'x').format('MMMM')}
              <ChevronRight className='cursor-pointer' onClick={() => { changeDate(1) }} />
            </div>
          </div>
        </CardHeader>
        <CardBody>
          <UILoader blocking={block} loader={<Loader />}>
            <div className='invoice-list-dataTable react-dataTable'>
              <DataTable
                noHeader
                sortServer
                responsive
                columns={columns}
                sortIcon={<ChevronDown />}
                className='react-dataTable'
                data={dataToRender()}
              />
            </div>
          </UILoader>
        </CardBody>
      </Card>
    </div>
  )
}

export default Attendance
