// ** React Imports
import moment from 'moment'
import { Badge } from 'reactstrap'
const dateFormat = (value) => {
  return moment(value, 'x').format("MMM DD, YYYY")
}
const timeFormat = (value) => {
  return moment(value, 'x').format("H:m A")
}

const msToTime = (ms) => {
  let seconds = ms / 1000
  const hours = parseInt(seconds / 3600)
  seconds = seconds % 3600
  const minutes = parseInt(seconds / 60)
  seconds = seconds % 60
  return `${hours} Hrs : ${minutes} Mins`
}
const statusArr = ['', 'Early', 'Regular', 'Late']
const statusColorArr = ['', 'light-primary', 'light-success', 'light-warning']

export const columns = [
  {
    name: 'Date',
    sortable: true,
    sortField: 'id',
    maxWidth: '157px',
    cell: row => <span>{dateFormat(row.actualdate)}</span>
  },
  {
    name: 'Punch In',
    sortable: true,
    sortField: 'id',
    maxWidth: '137px',
    cell: row => <span>{timeFormat(row.punchin)}</span>
  },
  {
    name: 'Punch Out',
    sortable: true,
    sortField: 'id',
    maxWidth: '167px',
    cell: row => <span>{row.punchout ? timeFormat(row.punchout) : ''}</span>
  },
  {
    name: 'Total Hours',
    sortable: true,
    sortField: 'id',
    maxWidth: '167px',
    cell: row => <span>{msToTime(row.totalhours)}</span>
  },
  {
    name: 'Punch In Location',
    maxWidth: '290px',
    sortable: true,
    sortField: 'location',
    selector: row => row.locationname,
    cell: row => (

      <span className='text-capitalize text-truncate'>
        <a href={`https://www.google.com/maps/search/${row?.locationname}`} target='_blank'>{row.locationname}</a>
      </span>
    )
  },
  {
    name: 'Punch Out Location',
    maxWidth: '290px',
    sortable: true,
    sortField: 'location',
    selector: row => row.punchoutlocation,
    cell: row => (

      <span className='text-capitalize text-truncate'>
        <a href={`https://www.google.com/maps/search/${row?.punchoutlocation}`} target='_blank'>{row.punchoutlocation}</a>
      </span>
    )
  }, 
  {
    name: 'Status',
    sortable: true,
    sortField: 'id',
    maxWidth: '147px',
    cell: row => <Badge color={statusColorArr[row.status]}>{statusArr[row.status]}</Badge>
  }

]
