// ** React Imports
import { useState, useEffect, Fragment } from 'react'

import { Download, Edit, MoreVertical, Plus, Trash } from 'react-feather'
import { Button, Card, CardHeader, Col, DropdownItem, DropdownMenu, DropdownToggle, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader, Row, Table, UncontrolledDropdown } from 'reactstrap'
import classnames from 'classnames'
// ** Store & Actions
import { getTimesheetList } from '@src/views/apps/timesheet/store/index.js'
import { useDispatch, useSelector } from 'react-redux'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
// ** Styles
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import axios from '@src/configs/axios/axiosConfig'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()

const TimeSheet = (props) => {
  // ** Store Vars
  const { tabId } = props
  const dispatch = useDispatch()
  const store = useSelector(state => state.timesheet)
  const { id } = useParams()
  // ** States
  const [value] = useState('')
  const [rowsPerPage] = useState(6)
  const [sort] = 'desc'
  const [sortColumn] = 'createdon'
  const [formModal, setFormModal] = useState(false)
  const [startTime, setStartTime] = useState()
  const [endTime, setEndTime] = useState()
  const [filterDate, setFilterDate] = useState(moment().unix())
  const [hourlyRate, setHourlyRate] = useState(0)
  const [selectedTimeSheet, setSelectedTimesheet] = useState(null)
  const [name, setName] = useState('')

  useEffect(() => {
    if (tabId === 'timesheet') {
      dispatch(
        getTimesheetList({
          sort,
          q: value,
          sortColumn,
          perPage: rowsPerPage,
          userId: id,
          date: moment.unix(filterDate).startOf('day').valueOf(),
          enddate: moment.unix(filterDate).endOf('day').valueOf()
        })
      )
    }
  }, [tabId])

  const getEmployeeDet = () => {
    axios.post('/organizationusers/getjobinfo', { id })
      .then((res) => {
        setHourlyRate(res.data.organizationusers.hourlyrate || 0)
      }).catch((err) => {
        console.log(err)
      })
  }

  const dataToRender = () => {
    const filters = {
      q: value
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data.slice(0, rowsPerPage)
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return store.allData.slice(0, rowsPerPage)
    }
  }

  const closeModal = () => {
    setFormModal(false)
  }

  const handleStartDate = (val) => {
    setFilterDate(val)

    dispatch(
      getTimesheetList({
        q: value,
        sortColumn,
        userId: id,
        date: moment.unix(val).startOf('day').valueOf(),
        enddate: moment.unix(val).endOf('day').valueOf()
      })
    )
  }

  const cancellModal = () => {
    setFormModal(false)
    setName('')
    setStartTime('')
    setEndTime('')
  }

  const editModal = (val) => {
    setFormModal(true)
    setSelectedTimesheet(val)
    setName(val.taskname)
    setStartTime(moment(val.starttime, 'x').format('HH:mm'))
    setEndTime(moment(val.endtime, 'x').format('HH:mm'))
  }

  const deleteTimeSheet = async (id) => {

    await axios.post('/tasktimesheets/delete', { id, updatedBy: userId })
      .then(() => {
        dispatch(
          getTimesheetList({
            sort,
            q: value,
            userId: id,
            date: moment.unix(filterDate).startOf('day').valueOf(),
            enddate: moment.unix(filterDate).endOf('day').valueOf()
          })
        )
      })
  }

  const deletefn = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await deleteTimeSheet(id)
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'TImesheet has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const dateFormattime = (value) => {
    if (value?.length === 13) {
      return moment(value, 'x').format("h:m a")
    } else {
      return moment.unix(value).format("h:m a")
    }
  }

  const hoursFormat = (row) => {

    const decimalTimeString = row.totalhours
    let decimalTime = parseFloat(decimalTimeString)
    decimalTime = decimalTime * 60 * 60
    let hours = Math.floor((decimalTime / (60 * 60)))
    decimalTime = decimalTime - (hours * 60 * 60)
    let minutes = Math.floor((decimalTime / 60))

    if (hours < 10) {
      hours = `0${hours}`
    }
    if (minutes < 10) {
      minutes = `0${minutes}`
    }

    return (`${isNaN(hours) ? '00' : hours}: ${isNaN(minutes) ? '00' : minutes}`)

  }

  const onSubmit = () => {
    const starttime = moment().set('hour', startTime.split(':')[0]).set('minutes', startTime.split(':')[1]).valueOf()
    const endtime = moment().set('hour', endTime.split(':')[0]).set('minutes', endTime.split(':')[1]).valueOf()
    const userid = id
    const start = moment().set('hour', startTime.split(':')[0]).set('minutes', startTime.split(':')[1])
    const end = moment().set('hour', endTime.split(':')[0]).set('minutes', endTime.split(':')[1])
    const duration = moment.duration(end.diff(start))
    const hours = duration.asHours()
    const amount = hourlyRate * hours

    axios.post('/tasktimesheets/manualentry', { type: true, name, id: selectedTimeSheet?.id, createdBy: userId, starttime, endtime, userid, amount, hourlyRate, totalhours: hours, taskId: id })
      .then(() => {
        dispatch(
          getTimesheetList({
            sort,
            q: value,
            sortColumn,
            perPage: rowsPerPage,
            userId: id,
            date: moment.unix(filterDate).startOf('day').valueOf(),
            enddate: moment.unix(filterDate).endOf('day').valueOf()
          })
        )
        cancellModal()
      })
      .catch(() => {

      })
  }

  useEffect(() => {
    getEmployeeDet()
  }, [])

  const hoursFormatTemp = (totalhours, flg) => {

    const decimalTimeString = totalhours
    let decimalTime = parseFloat(decimalTimeString)
    decimalTime = decimalTime * 60 * 60
    let hours = Math.floor((decimalTime / (60 * 60)))
    decimalTime = decimalTime - (hours * 60 * 60)
    let minutes = Math.floor((decimalTime / 60))

    if (hours < 10) {
      hours = `0${hours}`
    }
    if (minutes < 10) {
      minutes = `0${minutes}`
    }
    if (flg) {
      return isNaN(hours) ? 0 : hours
    } else {
      return isNaN(minutes) ? 0 : minutes
    }

  }

  const totalHoursfn = () => {

    return dataToRender().reduce(function (memo, t) { // sum the times
      const hour = hoursFormatTemp(t.totalhours, true)
      const min = hoursFormatTemp(t.totalhours, false)
      memo[0] += parseInt(hour, 10)
      memo[1] += parseInt(min, 10)
      if (memo[1] >= 60) {
        memo[0] += ~~(memo[1] / 60)
        memo[1] = memo[1] % 60
      }
      return memo
    }, [0, 0]).map(function (t) { // map to the desired format
      if (t < 10) {
        return `0${t}`
      }
      return t
    }).join(' : ')

  }

  return (
    <div className='invoice-list-wrapper'>
      <Card>
        <CardHeader>
          <div>
            <Flatpickr
              val={filterDate}
              placeholder='Select Date'
              onChange={(date, dateStr) => { handleStartDate(dateStr) }}
              options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
              className={classnames('due-date-picker')} />

          </div>
          <div className='d-flex'>
            <Button size='sm' color='primary' onClick={() => setFormModal(true)}><Plus size={16} /> Add Logs</Button>
            {/* <UncontrolledDropdown className='ms-1'>
              <DropdownToggle color='secondary' caret outline>
                <MoreVertical className='font-small-4 me-50' />
              </DropdownToggle>
              <DropdownMenu>
                <DropdownItem >
                  <Download className='font-small-4 me-50' />
                  <span className='align-middle'>Export</span>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown> */}
          </div>
        </CardHeader>

        <div className='invoice-list-dataTable react-dataTable'>

          <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
            <ModalHeader toggle={() => closeModal()}>Timesheet Log</ModalHeader>

            <ModalBody>
              <Row>
                <Label size='lg' className='form-label required'>No Task Timesheet Log</Label>
                <Col><Input type='text' value={name} onChange={(e) => { setName(e.target.value) }} /></Col>
              </Row>
              <Row>
                <Label size='lg' className='form-label required'>Star Time</Label>
                <Col><Input type='time' value={startTime} onChange={(e) => { setStartTime(e.target.value) }} /></Col>
              </Row>
              <Row>
                <Label size='lg' className='form-label required'>End Time</Label>
                <Col>
                  <Input type='time' value={endTime} onChange={(e) => { setEndTime(e.target.value) }} />
                </Col>
              </Row>
            </ModalBody>
            <ModalFooter>
              <Button color='warning' onClick={() => closeModal()}>
                Cancel
              </Button>
              <Button color='primary' onClick={() => { onSubmit() }}>Submit</Button>
            </ModalFooter>
          </Modal >

          <Table>
            <thead>
              <tr>
                <th>Client</th>
                <th>Task</th>
                <th className='text-center'>Start</th>
                <th className='text-center'>End</th>
                <th className='text-center'>Total</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {dataToRender()?.map((item, k) => {
                return (
                  <tr key={k}>
                    <td className='p-75'>{item.clientname ? (item.clientname?.length < 21 ? item.clientname : `${item.clientname?.substr(0, 20)}...`) : 'Non-Task Linked Time'}</td>
                    <td className='p-75'>{item?.taskname?.length < 21 ? item.taskname : `${item.taskname?.substr(0, 20)}...`}</td>
                    <td className='p-75 text-center'>{dateFormattime(item.starttime)}</td>
                    <td className='p-75 text-center'>{(item.endtime !== null ? dateFormattime(item.endtime) : '-')}</td>
                    <td className='p-75 text-center'>{hoursFormat(item || '0')}</td>
                    <td className='p-75 text-center'>
                      {item.timesheettype &&
                        <Fragment>
                          <Edit size={16} onClick={() => editModal(item)} />
                          <Trash size={16} onClick={() => { deletefn(item.id) }} />
                        </Fragment>
                      }
                    </td>
                  </tr>
                )
              })
              }
              <tr>
                <td colSpan={4} className='text-end'>
                  <h5>Total</h5>
                </td>
                <td colSpan={1} className='text-center'>
                  <h5>{totalHoursfn()}</h5>
                </td>
                <td></td>
              </tr>
            </tbody>
          </Table>
        </div>
      </Card >
    </div >
  )
}

export default TimeSheet
