// ** React Imports
import { Fragment, useState } from 'react'
import { Link } from 'react-router-dom'
// ** Reactstrap Imports
import { Card, CardBody, Button, Label, Input } from 'reactstrap'
// ** Custom Components
import axios from '@src/configs/axios/axiosConfig'
import defaultAvatar from '@src/assets/images/avatars/avatar-blank.png'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import toast from 'react-hot-toast'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()

const UserInfoCard = ({ selectedUser }) => {

  // ** States
  const [avatar, setAvatar] = useState(selectedUser.avatar ? selectedUser.avatar : defaultAvatar)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const uploadFiles = (file) => {

    const formData = new FormData()
    formData.append(`files[0]`, file)
    formData.append('organizationId', activeOrgId)
    formData.append('createdBy', userId)
    setBlock(true)
    axios.post('/google/profileUploads', formData).then(() => {
      toast.success('User Profile Picture Uploaded Successfully', { position: "bottom-right" })
      setBlock(false)
    }).catch((err) => { console.log(err) })
  }

  const onChange = e => {
    const reader = new FileReader(),
      files = e.target.files
    reader.onload = function () {
      setAvatar(reader.result)
    }
    reader.readAsDataURL(files[0])

    uploadFiles(e.target.files[0])

  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <CardBody>
            <div className='user-avatar-section'>
              <div className='d-flex align-items-center flex-column'>
                <div className='d-flex flex-column align-items-center text-center'>
                  <div className='user-info'>
                    <h4>{selectedUser !== null ? selectedUser.name : 'Eleanor Aguilar'}</h4>
                  </div>
                  <div className='me-25 mb-1'>
                    {(selectedUser.fileid !== null) ? <img className='rounded me-50' src={`https://drive.google.com/thumbnail?id=${selectedUser.fileid}&authuser=0`} referrerPolicy="no-referrer" alt='Organization image' height='100' width='100' /> : <img className='rounded me-50' src={avatar} alt='Generic placeholder image' height='100' width='100' />
                    }
                  </div>
                  <Button tag={Label} className='mb-75 me-75' size='sm' color='primary'>
                    Upload
                    <Input type='file' onChange={onChange} hidden accept='image/*' />
                  </Button>
                </div>
              </div>
            </div>
            <h4 className='fw-bolder border-bottom pb-50 mb-1'>Details</h4>
            <div className='info-container'>
              {selectedUser !== null ? (
                <ul className='list-unstyled'>
                  <li className='mb-75'>
                    <span className='fw-bolder me-25'>Email:</span>
                    <span>{selectedUser.email}</span>
                  </li>
                  <li className='mb-75'>
                    <span className='fw-bolder me-25'>Role:</span>
                    <span className='text-capitalize'>{selectedUser.rolename}</span>
                  </li>
                  <li className='mb-75'>
                    <span className='fw-bolder me-25'>Department:</span>
                    <span>{selectedUser.departmentname}</span>
                  </li>
                  <li className='mb-75'>
                    <span className='fw-bolder me-25'>Designation:</span>
                    <span>{selectedUser.designationname}</span>
                  </li>
                </ul>
              ) : null}
            </div>
            <div className='d-flex justify-content-center pt-2'>
              <Button className='ms-1' color='danger' outline tag={Link} to={`/team/list`}>
                Cancel
              </Button>
            </div>
          </CardBody>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default UserInfoCard
