// ** React Imports
import { Fragment, useEffect, useState } from 'react'
// ** Reactstrap Imports
import { Nav, NavItem, NavLink, TabContent, TabPane } from 'reactstrap'
// ** Icons Imports
import { Bookmark, Bell, CheckSquare, Edit2 } from 'react-feather'
// ** User Components
import Attendance from './Attendance'
import JobInfo from './JobInfo'
import TimeSheet from './TimeSheet'
import Leave from './Leave'
import Holidays from './Holidays'
import { Link, useLocation } from 'react-router-dom'
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()
const UserTabs = ({ selectedUser }) => {

  const search = useLocation().search
  const searchParams = new URLSearchParams(search)
  const [active, setActive] = useState('')

  useEffect(() => {
    const tabId = searchParams.get('tabId')
    setActive(tabId)
  }, [searchParams])
  
  return (
    <Fragment>
      <Nav pills className='mb-2'>
        <NavItem>
          <NavLink active={active === 'jobinfo'} tag={Link} to={`/profile/view/${userId}?tabId=jobinfo`}>
            <CheckSquare className='font-medium-3 me-50' />
            <span className='fw-bold'>Job Info</span>
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink active={active === 'profileattendance'} tag={Link} to={`/profile/view/${userId}?tabId=profileattendance`}>
            <Bookmark className='font-medium-3 me-50' />
            <span className='fw-bold'>Attendance</span>
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink active={active === 'timesheet'} tag={Link} to={`/profile/view/${userId}?tabId=timesheet`}>
            <Bell className='font-medium-3 me-50' />
            <span className='fw-bold'>Time Sheet</span>
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink active={active === 'profileholidays'} tag={Link} to={`/profile/view/${userId}?tabId=profileholidays`}>
            <Bell className='font-medium-3 me-50' />
            <span className='fw-bold'>Holidays</span>
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink active={active === 'leaves'} tag={Link} to={`/profile/view/${userId}?tabId=leaves`}>
            <Bell className='font-medium-3 me-50' />
            <span className='fw-bold'>Leave</span>
          </NavLink>
        </NavItem>
      </Nav>
      <TabContent activeTab={active}>
        <TabPane tabId='jobinfo'>
          <JobInfo tabId={active} selectedUser={selectedUser} />
        </TabPane>
        <TabPane tabId='profileattendance'>
          <Attendance tabId={active} />
        </TabPane>
        <TabPane tabId='timesheet'>
          <TimeSheet tabId={active} />
        </TabPane>
        <TabPane tabId='profileholidays'>
          <Holidays tabId={active} />
        </TabPane>
        <TabPane tabId='leaves'>
          <Leave tabId={active} />
        </TabPane>
      </TabContent>
    </Fragment>
  )
}
export default UserTabs
