// ** Reactstrap Imports
import { Card, CardBody, Row, Col } from 'reactstrap'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import { useParams } from 'react-router-dom'
import { useEffect, useState } from 'react'
import axios from '@src/configs/axios/axiosConfig'
import moment from 'moment'
const JobInfo = (props) => {
  const { tabId, selectedUser } = props
  const [jobInfo, setJobInfo] = useState({})
  const { id } = useParams()
  const dateFormat = (val) => {
    if (val === undefined) {
      return
    }
    if (val.length === 10) {
      return moment.unix(val).format("MMM DD, Y")
    } else {
      return moment(val, 'x').format("MMM DD, Y")
    }
  }
  const getEmployeeDet = () => {
    axios.post('/organizationusers/getjobinfo', { id })
      .then((res) => {
        setJobInfo({
          dateofhire: res.data.organizationusers.dateofhire || '',
          dateofbirth: res.data.organizationusers.dateofbirth || '',
          hourlyrate: res.data.organizationusers.hourlyrate || '',
          salary: res.data.organizationusers.salary || ''
        })
      }).catch((err) => {
        console.log(err)
      })
  }

  useEffect(() => {
    if (tabId === 'jobinfo') {
      getEmployeeDet()
    }
  }, [tabId])

  return (
    <Card>
      <CardBody>
        <dl>
          <Row>
            <Col sm='3'>
              <dt>Job Title</dt>
            </Col>
            <Col sm='9'>
              <dd> {selectedUser.rolename || ''}</dd>
            </Col>
          </Row>
        </dl>
        <dl>
          <Row>
            <Col sm='3'>
              <dt>Date of Birth</dt>
            </Col>
            <Col sm='9'>
              <span>{jobInfo.dateofbirth ? dateFormat(jobInfo.dateofbirth) : '-'}</span>
            </Col>
          </Row>
        </dl>
        <dl>
          <Row>
            <Col sm='3'>
              <dt>Date of Hire</dt>
            </Col>
            <Col sm='9'>
              <span>{dateFormat(jobInfo.dateofhire)}</span>
            </Col>
          </Row>
        </dl>
        <dl>
          <Row>
            <Col sm='3'>
              <dt>Salary</dt>
            </Col>
            <Col sm='9'>
              <span>{jobInfo.salary}</span>
            </Col>
          </Row>
        </dl>
        <dl>
          <Row>
            <Col sm='3'>
              <dt>Hourly Rate</dt>
            </Col>
            <Col sm='9'>
              <span>{jobInfo.hourlyrate}</span>
            </Col>
          </Row>
        </dl>
      </CardBody>
    </Card>
  )
}
export default JobInfo
